a {
    text-decoration: none;
    color: rgb(241, 241, 241);
}

.nav {
    margin-top: 15px;
    display: flex;
}

.nav .idNumber {
    width: 330px;
    font-size: 10pt;
    display: flex;
}

.mainMenu {
    width: 1280px;
    height: 22px;
    font-size: 8pt;
    letter-spacing: 0.5px;
}

.mainMenu li {
    float: left;
    cursor: pointer;
    border: 1px solid transparent;
}

.mainMenu li a {
    padding: 5px 6px;
    border: 1px solid transparent;
}

.mainMenu .mainFirst {
    border-right: 1px solid rgb(56, 56, 56);
}

.mainMenu .mainQa {
    border-right: 1px solid rgb(56, 56, 56);
    border-left: 1px solid rgb(56, 56, 56);
}

.mainMenu li:hover a,
.mainMenu .mainMenuOn {
    border: 1px solid orange;
    border-radius: 5px 5px 0 0;
}